
.vuestic-switch {
  cursor: pointer;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
      -ms-flex-direction: row;
          flex-direction: row;
}
.vuestic-switch .vuestic-switch-option {
    padding: 0.313rem 0.375rem;
    border-top: 0.125rem solid transparent;
    border-bottom: 0.125rem solid #4ae387;
    background-color: transparent;
    -webkit-transition: all 0.15s ease;
    transition: all 0.15s ease;
    width: 50%;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-align: center;
        -ms-flex-align: center;
            align-items: center;
}
.vuestic-switch .vuestic-switch-option.active {
      color: #fff;
      background-color: #4ae387;
}
.vuestic-switch .vuestic-switch-option:not(.active):hover {
      color: #4ae387;
}



